'use server';
/**
 * @fileOverview Categorizes a user's stated reason for potential goal failure using AI.
 *
 * - categorizeFailureReason - A function that categorizes the failure reason.
 * - CategorizeFailureReasonInput - The input type for the categorizeFailureReason function.
 * - CategorizeFailureReasonOutput - The return type for the categorizeFailureReason function.
 */

import {ai} from '@/ai/genkit';
import {z} from 'genkit';

const CategorizeFailureReasonInputSchema = z.object({
  failureReason: z.string().describe("The user's stated reason for why their goal might fail."),
});
export type CategorizeFailureReasonInput = z.infer<typeof CategorizeFailureReasonInputSchema>;

const CategorizeFailureReasonOutputSchema = z.object({
  category: z.enum(['POSSIBLE_OUTCOME', 'PAST_TRAIT', 'ABSOLUTE_TRUTH'])
    .describe('The identified category of the failure reason.'),
  categorizedStatement: z.string()
    .describe('The failure reason rephrased according to the identified category and its template.'),
});
export type CategorizeFailureReasonOutput = z.infer<typeof CategorizeFailureReasonOutputSchema>;

export async function categorizeFailureReason(input: CategorizeFailureReasonInput): Promise<CategorizeFailureReasonOutput> {
  return categorizeFailureReasonFlow(input);
}

const categorizeFailureReasonPrompt = ai.definePrompt({
  name: 'categorizeFailureReasonPrompt',
  input: {schema: CategorizeFailureReasonInputSchema},
  output: {schema: CategorizeFailureReasonOutputSchema},
  prompt: `You are an AI assistant helping users process potential failures related to their goals.
Analyze the user's stated reason for potential failure and categorize it into one of the following three categories.
Then, rephrase the failure reason according to the template for the identified category.

Categories and Templates:

1.  **POSSIBLE_OUTCOME**: The reason describes a potential external event or outcome that might occur.
    *   Template: "It is possible to fail because it is possible that [original reason, rephrased for grammatical completeness if needed. E.g., if user says 'bad weather', rephrase to 'the weather will be bad']"
    *   Example Input: "the project might get delayed"
    *   Example Output Statement: "It is possible to fail because it is possible that the project might get delayed"
    *   Example Input: "I don't get the promotion"
    *   Example Output Statement: "It is possible to fail because it is possible that I won't get the promotion"

2.  **PAST_TRAIT**: The reason describes a personal trait, past behavior, or recurring pattern of the user.
    *   Template: "It is possible to fail because in the past I have been [trait/behavior from original reason, e.g., 'lazy', 'procrastinating', 'afraid to ask for help']"
    *   Example Input: "I am lazy"
    *   Example Output Statement: "It is possible to fail because in the past I have been lazy"
    *   Example Input: "I tend to procrastinate"
    *   Example Output Statement: "It is possible to fail because in the past I have tended to procrastinate"
    *   Example Input: "my usual fear of public speaking"
    *   Example Output Statement: "It is possible to fail because in the past I have been afraid of public speaking"


3.  **ABSOLUTE_TRUTH**: The reason describes something the user perceives as an unchangeable fact, an absolute external barrier, or a deeply ingrained limiting belief, which they express as a feeling about the situation.
    *   Template: "It is possible to fail because I feel [original reason, rephrased for grammatical completeness if needed. E.g., if user says 'the economy is bad', rephrase to 'the economy is bad'; if user says 'success is impossible for me', rephrase to 'success is impossible for me']"
    *   Example Input: "the economy is bad"
    *   Example Output Statement: "It is possible to fail because I feel the economy is bad"
    *   Example Input: "success is impossible for me"
    *   Example Output Statement: "It is possible to fail because I feel success is impossible for me"
    *   Example Input: "There's not enough time in the day"
    *   Example Output Statement: "It is possible to fail because I feel there's not enough time in the day"


User's Failure Reason:
{{{failureReason}}}

Return ONLY the identified category and the rephrased statement based on the templates.
The 'categorizedStatement' should be the rephrased version.
`,
  config: {
    temperature: 0.3, // Lower temperature for more deterministic categorization
  }
});

const categorizeFailureReasonFlow = ai.defineFlow(
  {
    name: 'categorizeFailureReasonFlow',
    inputSchema: CategorizeFailureReasonInputSchema,
    outputSchema: CategorizeFailureReasonOutputSchema,
  },
  async (input) => {
    const {output} = await categorizeFailureReasonPrompt(input);
    if (!output) {
      // Fallback or error handling if LLM doesn't return expected output
      console.error("LLM did not return expected output for failure reason categorization.");
      // Return a default or throw an error
      return { 
        category: 'ABSOLUTE_TRUTH', // Default category
        categorizedStatement: `It is possible to fail because I feel ${input.failureReason}` // Default statement based on new rule
      };
    }
    return output;
  }
);

